import styles from './styles/sideMenu.module.css'

function SideMenu ({menuClick, changeMenuClicked}) {
    return (
        <div className={`${menuClick ? "transform -left-0 transition-all ease-out duration-500": "-translate-y-24"} fixed z-20 h-full w-11/12 bg-white`}
            onClick={changeMenuClicked}
        >
            <h1>Side Menu</h1>
        </div>
    )
}

export default SideMenu